/* Basic CSS for Dashboard Screen */

.dashboard-container {
  display: flex;
  height: 100vh;
  background-color: var(--bg-page);
  overflow: hidden;
  font-family: Arial, sans-serif;
}

/* Sidebar Styles */
.sidebar {
  background-color: var(--sidebar-bg);
  display: flex;
  flex-direction: column;
  width: 256px;
}

.sidebar-collapsed {
  width: 80px;
}

.sidebar-header {
  height: 64px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid var(--sidebar-border);
}

.sidebar-logo {
  color: var(--sidebar-text);
  font-weight: bold;
  font-size: 1.25rem;
  text-transform: uppercase;
  padding: 0 0.5rem;
}

.sidebar-menu {
  flex: 1;
  overflow-y: auto;
  padding: 1rem 0.75rem;
}

.sidebar-item {
  width: 100%;
  display: flex;
  align-items: center;
  padding: 0.75rem 1rem;
  margin-bottom: 0.25rem;
  border-radius: 0.5rem;
  border: none;
  background: transparent;
  color: var(--sidebar-text-dim);
  cursor: pointer;
  font-size: 0.875rem;
}

.sidebar-item:hover {
  background-color: var(--sidebar-hover);
  color: var(--sidebar-text);
}

.sidebar-item.active {
  background-color: var(--sidebar-hover);
  color: var(--sidebar-text);
}

.sidebar-item span {
  margin-left: 0.75rem;
}

.sidebar-divider {
  height: 1px;
  background-color: var(--sidebar-border);
  margin: 0.5rem;
}

.sidebar-footer {
  padding: 1rem;
  border-top: 1px solid var(--sidebar-border);
}

.logout-button {
  display: flex;
  align-items: center;
  color: var(--sidebar-text-dim);
  background: none;
  border: none;
  cursor: pointer;
  width: 100%;
  padding: 0.5rem;
}

.logout-button:hover {
  color: var(--sidebar-text);
}

.logout-button span {
  margin-left: 0.75rem;
}

.hidden {
  display: none;
}

/* Main Content Styles */
.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.top-nav {
  height: 64px;
  background-color: var(--bg-primary);
  box-shadow: var(--shadow-card);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 1.5rem;
}

.nav-left {
  display: flex;
  align-items: center;
}

.menu-button {
  padding: 0.5rem;
  border-radius: 0.375rem;
  border: none;
  background: none;
  color: var(--text-muted);
  cursor: pointer;
}

.menu-button:hover {
  background-color: var(--bg-tertiary);
}

.page-title {
  margin-left: 1rem;
  font-size: 1.25rem;
  font-weight: bold;
  color: var(--text-primary);
}

.nav-right {
  display: flex;
  align-items: center;
}

.search-container {
  position: relative;
  margin-right: 1rem;
}

.search-input {
  padding: 0.5rem 2.5rem;
  border-radius: 20px;
  background-color: var(--bg-tertiary);
  border: none;
  font-size: 0.875rem;
  width: 12rem;
}

.search-input:focus {
  outline: none;
  border: 2px solid var(--brand-primary);
  width: 16rem;
}

.search-icon {
  position: absolute;
  left: 0.75rem;
  top: 50%;
  margin-top: -8px;
  height: 1rem;
  width: 1rem;
  color: var(--text-tertiary);
}

.notification-button {
  position: relative;
  padding: 0.5rem;
  border-radius: 50%;
  border: none;
  background: none;
  color: var(--text-muted);
  cursor: pointer;
  margin-right: 1rem;
}

.notification-button:hover {
  background-color: var(--bg-tertiary);
}

.notification-badge {
  position: absolute;
  top: 0.25rem;
  right: 0.25rem;
  height: 0.5rem;
  width: 0.5rem;
  background-color: var(--danger);
  border-radius: 50%;
  border: 2px solid var(--bg-primary);
}

.user-profile {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 0.25rem;
  border-radius: 0.5rem;
}

.user-profile:hover {
  background-color: var(--bg-secondary);
}

.avatar {
  height: 2rem;
  width: 2rem;
  border-radius: 50%;
  background-color: var(--brand-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-inverse);
  font-size: 0.875rem;
  margin-right: 0.5rem;
}

.user-info {
  text-align: left;
  margin-right: 0.5rem;
}

.user-name {
  font-size: 0.875rem;
  font-weight: 600;
  color: var(--text-dark);
  margin: 0;
  padding: 0;
}

.user-role {
  font-size: 0.75rem;
  color: var(--text-secondary);
  margin: 0;
  padding: 0;
}

.dropdown-icon {
  color: var(--text-tertiary);
}

.content-area {
  flex: 1;
  overflow-y: auto;
  background-color: var(--bg-secondary);
}

/* Students Page Styles */
.students-page {
  padding: 1.5rem;
}

.students-header {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-bottom: 1.5rem;
}

.students-header h2 {
  margin: 0;
  font-size: 1.5rem;
  color: var(--brand-primary);
}

.students-table-container {
  background: var(--bg-primary);
  border-radius: 0.75rem;
  box-shadow: var(--shadow-card);
  overflow: hidden;
}

.students-list-table {
  width: 100%;
  border-collapse: collapse;
}

.students-list-table th,
.students-list-table td {
  padding: 1rem 1.5rem;
  text-align: left;
  border-bottom: 1px solid var(--border-color);
}

.students-list-table th {
  background-color: var(--bg-secondary);
  font-weight: 600;
  color: var(--text-dark);
  font-size: 0.875rem;
  text-transform: uppercase;
}

.students-list-table td {
  color: var(--text-primary);
  font-size: 0.95rem;
}

.students-list-table tbody tr:hover {
  background-color: var(--bg-tertiary);
}

.students-list-table tbody tr:last-child td {
  border-bottom: none;
}